.nav-container {
    display: flex;
    justify-content: space-around;
    border-bottom: 1px solid $clr1;
    background-color: $clr4;
    .navlink {
        padding: 10px;
        border-radius: 5px;
        color: $clr3;
        text-decoration: none;
        font-size: 1.5rem;
        &:hover {
            transition: 0.5s;
            background-color: $clr2;
        }
    }
}
